import React, { useEffect } from "react";
import "./jeux.vr.scss";

function JeuxVR() {
	return(
	<div className="containerJeuxVR">
		<div className="leftPartJeuxVR">
			<img src="imageJeux" alt="" />
		</div>
		<div className="rightPartJeuxVR">
			<h1 className="titleRightJeuxVR">Dcouvrez nos recommandations de jeux pour vos vnements</h1>
			<p className="textRightJeuxVR">Des jeux VR multijoueurs incroyables pour s'amuser en quipe! De l'escape game aux jeux en collaborations, dcouvrez nos recommandations d'exprience VR pour un vnement russi!</p>
			<button className="boutonNosJeux">Nos Jeux</button>
		</div>
	</div>
	)
}

export default JeuxVR;

